.change-form-container {
  display: flex; }
  .change-form-container .edit-detail-container {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    padding-left: 20px;
    margin: 30px 0;
    flex-direction: column; }
    .change-form-container .edit-detail-container .edit-detail-inner-container {
      display: flex; }
    .change-form-container .edit-detail-container .edit-detail-header {
      font-size: 14px;
      font-weight: 600;
      color: #354F53;
      margin-bottom: 10px; }
    .change-form-container .edit-detail-container .edit-detail-label-container,
    .change-form-container .edit-detail-container .edit-detail-value-container {
      display: flex;
      flex-direction: column; }
    .change-form-container .edit-detail-container .edit-detail-label-container {
      font-size: 14px;
      font-weight: 600;
      color: #46737A;
      margin-right: 27px; }
    .change-form-container .edit-detail-container .edit-detail-value-container {
      font-size: 14px;
      font-weight: 600;
      color: #354F53; }
      .change-form-container .edit-detail-container .edit-detail-value-container .detail-value {
        position: relative; }
        .change-form-container .edit-detail-container .edit-detail-value-container .detail-value .tooltip-button {
          width: 16px;
          height: 16px;
          border: solid 1px #00D7AF;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-content: center;
          position: absolute;
          top: 3px;
          right: -44px; }
          .change-form-container .edit-detail-container .edit-detail-value-container .detail-value .tooltip-button:hover {
            cursor: pointer; }
          .change-form-container .edit-detail-container .edit-detail-value-container .detail-value .tooltip-button p {
            height: 100%;
            margin-top: 0;
            text-align: center;
            color: #00D7AF;
            font-size: 10px;
            font-weight: 200; }
      .change-form-container .edit-detail-container .edit-detail-value-container .white {
        color: white; }
  .change-form-container .edit-container {
    width: 30px;
    height: 30px;
    position: absolute;
    display: flex;
    right: -50px;
    justify-content: center;
    align-items: center;
    margin-top: -6px;
    top: 0; }
    .change-form-container .edit-container img {
      width: 30px;
      height: 30px; }
      .change-form-container .edit-container img:hover {
        filter: brightness(80%); }
      .change-form-container .edit-container img:active {
        width: 30px;
        height: 30px; }
  .change-form-container .tooltip-text {
    text-align: center;
    padding: 10px 0;
    font-size: 14px;
    font-weight: 600;
    color: #354F53; }
