.enquiry-container {
  width: 100%;
  min-height: 288px;
  border: solid 1px #C8E0E4; }
  @media screen and (max-width: 960px) {
    .enquiry-container {
      margin-bottom: 80px; } }
  .enquiry-container .upper-container {
    width: 100%;
    min-height: 120px;
    background-image: linear-gradient(96deg, #00D7AF, #46737A);
    padding: 36px 30px;
    display: flex; }
    .enquiry-container .upper-container a,
    .enquiry-container .upper-container .left-text {
      margin: auto;
      margin-left: 0;
      max-width: 500px;
      font-size: 18px;
      font-weight: 200;
      line-height: normal;
      color: white; }
    .enquiry-container .upper-container .right-text {
      justify-content: flex-end;
      margin: auto;
      margin-right: 0;
      display: flex; }
      .enquiry-container .upper-container .right-text img {
        margin-right: 20px; }
      .enquiry-container .upper-container .right-text span {
        font-size: 48px;
        font-weight: bold;
        color: white; }
    @media screen and (max-width: 960px) {
      .enquiry-container .upper-container {
        flex-direction: column; }
        .enquiry-container .upper-container .left-text,
        .enquiry-container .upper-container .right-text {
          margin: auto; }
        .enquiry-container .upper-container .left-text {
          text-align: center; } }
    @media screen and (max-width: 560px) {
      .enquiry-container .upper-container {
        padding: 16px 20px; }
        .enquiry-container .upper-container .left-text {
          max-width: 217px;
          font-size: 12px; }
        .enquiry-container .upper-container .right-text {
          margin-top: 20px; }
          .enquiry-container .upper-container .right-text img {
            width: 21px;
            height: 21px;
            margin-top: 5px; }
          .enquiry-container .upper-container .right-text span {
            font-size: 20px; } }
  .enquiry-container .lower-container {
    display: flex;
    flex-direction: column;
    padding: 5px;
    padding-bottom: 20px; }
    .enquiry-container .lower-container .lower-main {
      font-size: 24px;
      font-weight: normal;
      color: #00D7AF;
      line-height: 1.54;
      letter-spacing: -0.4px;
      text-align: center; }
    .enquiry-container .lower-container .lower-sub {
      font-size: 18px;
      font-weight: 200;
      color: #99BBC1;
      text-align: center;
      line-height: 1; }
    @media screen and (max-width: 560px) {
      .enquiry-container .lower-container .lower-main {
        margin-top: 20px;
        font-size: 14px; }
      .enquiry-container .lower-container .lower-sub {
        font-size: 12px; } }
