.accordion-border {
  border: solid 1px #C8E0E4;
  border-top: none; }

.accordion-opened {
  border-top: solid 1px #C8E0E4; }

.accordion-container {
  width: 100%; }
  .accordion-container .accordion-inner {
    padding: 0; }
    .accordion-container .accordion-inner .card {
      border-radius: 0;
      border: none; }
  .accordion-container .expand-container {
    border-top: solid 1px #C8E0E4;
    display: flex; }
    .accordion-container .expand-container .control-container {
      margin: auto;
      border: none;
      background-color: transparent; }
      .accordion-container .expand-container .control-container:focus {
        outline: none; }
      .accordion-container .expand-container .control-container .arrow-opened {
        transform: rotate(-180deg); }
      .accordion-container .expand-container .control-container span {
        margin-left: 13px;
        font-size: 18px;
        font-weight: 200;
        color: #99BBC1; }
  .accordion-container .warning-container {
    background-image: linear-gradient(92deg, #ffd138, #ffa41a);
    min-height: 36px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 5px; }
    .accordion-container .warning-container p {
      color: white;
      text-align: center; }
    @media screen and (max-width: 576px) {
      .accordion-container .warning-container {
        padding: 13px 28px; }
        .accordion-container .warning-container p {
          font-size: 14px; } }
