.product-slider {
  margin-top: 20px; }

.step-progress-description-container {
  max-width: 1036px;
  padding: 0 15px;
  margin-left: auto;
  margin-right: auto; }
  @media screen and (max-width: 576px) {
    .step-progress-description-container {
      margin-top: 0; } }
  .step-progress-description-container .main {
    min-height: 36px;
    text-align: center;
    font-size: 24px;
    font-weight: 800;
    color: #354F53;
    margin: 0; }
    @media screen and (max-width: 576px) {
      .step-progress-description-container .main {
        min-height: auto; } }
    @media screen and (max-width: 690px) {
      .step-progress-description-container .main {
        font-size: 18px; } }
  .step-progress-description-container .sub {
    font-size: 18px;
    font-weight: normal;
    text-align: center;
    color: #46737A; }
  .step-progress-description-container .back-button {
    position: absolute;
    left: 15px;
    font-size: 24px;
    font-weight: bold;
    color: #99BBC1; }
    @media screen and (max-width: 576px) {
      .step-progress-description-container .back-button {
        position: relative; } }
    .step-progress-description-container .back-button:hover {
      text-decoration: none;
      color: #00D7AF; }
