@media screen and (min-width: 576px) {
  .slider-summary-container {
    position: absolute;
    top: 0;
    right: 8px; } }

.slider-summary-container .prefix-suffix {
  font-size: 18px;
  font-weight: bold;
  color: #46737A; }

.slider-summary-container .value {
  font-size: 18px;
  font-weight: 800;
  color: #354F53; }

.slider-container {
  margin-bottom: 10px;
  position: relative;
  margin-left: auto;
  margin-right: auto; }
  .slider-container .line {
    width: 100%;
    height: 1px;
    border: solid 1px #C8E0E4; }
  .slider-container .line-green {
    position: absolute;
    top: 0;
    height: 1px;
    border: solid 1px #00D7AF; }
  .slider-container .color-knob {
    background-color: #00D7AF; }
  .slider-container .white-knob {
    background-color: #ffffff; }
  .slider-container .slider-knob {
    position: absolute;
    left: 0;
    width: 30px;
    height: 30px;
    box-shadow: 0 2px 8px 0 rgba(70, 115, 122, 0.2);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 3; }
    .slider-container .slider-knob:focus, .slider-container .slider-knob:active {
      outline: none; }
    .slider-container .slider-knob:hover {
      animation: zoom-in-out 1s infinite alternate;
      cursor: pointer; }

@keyframes zoom-in-out {
  from {
    transform: scale(1); }
  to {
    transform: scale(1.15); } }
    .slider-container .slider-knob .inner {
      width: 12px;
      height: 12px;
      border-radius: 50%; }
  .slider-container .knob-container {
    position: absolute;
    left: 0;
    top: -13px;
    height: 30px;
    width: calc(100% - 15px);
    display: flex;
    align-items: center; }
    .slider-container .knob-container .knob {
      position: absolute;
      left: 50px;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      z-index: 2; }
      .slider-container .knob-container .knob .desc {
        position: absolute;
        transform: translateY(-20px);
        min-width: 100px;
        font-size: 16px;
        font-weight: normal;
        color: #C8E0E4;
        text-align: center;
        transition: color 0.3s ease;
        bottom: 0; }
      .slider-container .knob-container .knob .desc-active {
        color: #46737A; }
    .slider-container .knob-container .knob-default {
      background-color: #C8E0E4;
      border: solid 7px white; }
    .slider-container .knob-container .knob-done {
      width: 30px;
      height: 30px;
      background-color: #00D7AF;
      background-image: url(../../../assets/images/common/Tick.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 70%;
      border: solid 4px white; }
    .slider-container .knob-container .knob:first-child {
      justify-content: flex-start; }
      .slider-container .knob-container .knob:first-child .desc {
        text-align: left;
        margin-left: -5px; }
    .slider-container .knob-container .knob:last-child {
      justify-content: flex-end; }
      .slider-container .knob-container .knob:last-child .desc {
        text-align: right; }
